import React, { useContext, useState } from 'react';
import {useNavigate} from 'react-router-dom';
import axios from 'axios'
import { Form, Button, Container, Row, Col, FormGroup } from 'react-bootstrap';
import { TokenContext } from './TokenContext';

const LoginComponent = () => {
    
    const [memberId, setMemberId] = useState("");
    const [memberPw, setMemberPw] = useState("");
    const [isForget, setIsForget] = useState(false);
    const {updateToken} = useContext(TokenContext);
    const navigate = useNavigate();
    
    const CheckLogin = (e) => {
        const checkloginUrl = '/api/opentalk/auth/login'

        if (memberId === ""){
            window.alert(" .");
        }
        else if (memberPw === ""){
            window.alert(" .");
        }
        else{
            axios.post(checkloginUrl, {
                memberId: memberId,
                memberPassword: memberPw,
            })
            .then((res) => {
                if (res.status === 200){
                    updateToken(res.headers['authorization']);
                    navigate("/opentalk/main");
                }
            })
            .catch((error)=>{
                if (error.response && error.response.status === 401) {
                    alert("   .");
                } else {
                    console.error('Error:', error);
                }
            });
        }
        
    }

   return (
        <Container style={{ minHeight: '100vh'}}>
            <Row>
                <Col md={{ span: 3, offset: 4 }} className="border border-#7B7B7B border-3 rounded-1 p-5" style={{backgroundColor:"#7B7B7B"}}>
                    <h2 style={{color:"white"}}>
                        <strong></strong>
                    </h2>
                    <hr/>
                    <Form onSubmit={CheckLogin}>
                        <FormGroup>
                            <Form.Label style={{color:"white"}}><strong></strong></Form.Label>
                            <Form.Control type="text" placeholder=" " value={memberId} onChange={(e) => setMemberId(e.target.value)}
                            style={{borderTopLeftRadius: "25px",
                                    borderBottomLeftRadius: "25px",
                                    borderTopRightRadius: "25px",
                                    borderBottomRightRadius: "25px"}} />
                        </FormGroup>
                        <FormGroup>
                            <Form.Label style={{color:"white"}}><strong></strong></Form.Label>
                            <Form.Control type="password" placeholder=" " value={memberPw} onChange={(e) => setMemberPw(e.target.value)} 
                            style={{borderTopLeftRadius: "25px",
                                    borderBottomLeftRadius: "25px",
                                    borderTopRightRadius: "25px",
                                    borderBottomRightRadius: "25px"}}/>
                        </FormGroup>
                        <hr/>
                        <div className="d-grid gap-2">
                            <Button variant='#CDCDCD' style={{backgroundColor:"#CDCDCD", 
                                                            borderTopLeftRadius: "25px",
                                                            borderBottomLeftRadius: "25px",
                                                            borderTopRightRadius: "25px",
                                                            borderBottomRightRadius: "25px"
                                                            }} onClick={CheckLogin} size='4'></Button>
                            <Button variant='#E0E0E0' style={{backgroundColor:"#E0E0E0", 
                                                            borderTopLeftRadius: "25px",
                                                            borderBottomLeftRadius: "25px",
                                                            borderTopRightRadius: "25px",
                                                            borderBottomRightRadius: "25px"}} onClick={() => navigate("/opentalk/member/enroll")}></Button>
                            <Button variant='warning' style={{borderTopLeftRadius: "25px",
                                                            borderBottomLeftRadius: "25px",
                                                            borderTopRightRadius: "25px",
                                                            borderBottomRightRadius: "25px"}} onClick={() => setIsForget(prevState => !prevState)}> ?</Button>
                            {isForget && (
                            <div className="d-grid gap-2">
                                <Button variant='warning' style={{borderTopLeftRadius: "25px",
                                                            borderBottomLeftRadius: "25px",
                                                            borderTopRightRadius: "25px",
                                                            borderBottomRightRadius: "25px"}} onClick={() => navigate("/opentalk/member/findId")}> </Button>
                                <Button variant='warning' style={{borderTopLeftRadius: "25px",
                                                            borderBottomLeftRadius: "25px",
                                                            borderTopRightRadius: "25px",
                                                            borderBottomRightRadius: "25px"}} onClick={() => navigate("/opentalk/member/authId")}> </Button>
                            </div>
                            )}
                            <Button variant='dark' style={{borderTopLeftRadius: "25px",
                                                            borderBottomLeftRadius: "25px",
                                                            borderTopRightRadius: "25px",
                                                            borderBottomRightRadius: "25px"}} onClick={() => navigate("/")}></Button>
                        </div>
                    </Form>
                </Col>
            </Row>
        </Container>
    );
}

export default LoginComponent;