import React, {useState} from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { Container, Row, Col, Button, Form, 
    FormControl, InputGroup,
    FormGroup} from 'react-bootstrap';

const EnrollComponent = (props) =>{
    const [memberId, setMemberId] = useState('');
    const [memberPw, setMemberPw] = useState('');
    const [memberName, setMemberName] = useState('');
    const [memberNickName, setMemberNickName] = useState('');
    const [memberEmail, setMemberEmail] = useState('');
    const [authNum, setAuthNum] = useState();
    const [inputNum, setInputNum] = useState();

    const [checkId, setCheckId] = useState(false);
    const [checkNickName, setCheckNickName] = useState(false);
    const [checkEmail, setCheckEmail] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');

    const [selectedImage, setSelectedImage] = useState(null);

    const navigate = useNavigate();

    const GetInputId = (event) =>{
        setMemberId(event.target.value);
    }

    const GetInputPw = (event) =>{
        setMemberPw(event.target.value);
    }

    const GetInputName = (event) =>{
        setMemberName(event.target.value);
    }

    const GetInputNickName = (event) =>{
        setMemberNickName(event.target.value);
    }

    const GetInputEmail = (event) =>{
        setMemberEmail(event.target.value);
    }

    const GetInputNum = (event) => {
        setInputNum(event.target.value);
    }

    const CheckIdDuplicate = () => {
        const data = new FormData();
        data.append("memberId", memberId);
        const checkUrl = `/api/opentalk/auth/signup/checkId`;
        axios.post(checkUrl, data).then((res)=>{
            if (res.data === true){
                alert(" .");
                setCheckId(false);
            }
            else{
                alert("  .");
                setCheckId(true);
            }
        }).catch((error)=>console.log(error))
    }

    const CheckNickNameDuplicate = () =>{
        const data = new FormData();
        data.append("memberNickName", memberNickName);
        const checkUrl = `/api/opentalk/auth/signup/checkNickName`;
        axios.post(checkUrl, data).then((res)=>{
            if (res.data === true){
                alert(" .");
                setCheckNickName(false);
            }
            else{
                alert("  .");
                setCheckNickName(true);
            }
        }).catch((error)=>console.log(error))
    }

    const CheckMail = () =>{
        const duplicateUrl = '/api/opentalk/auth/signup/checkEmail'
        const data = new FormData();
        data.append("memberEmail", memberEmail);
        axios.post(duplicateUrl, data)
        .then((res)=>{
            if (res.data === true){
                alert("  .");
            }
            else{
                const checkUrl = `/api/opentalk/enroll/mailSend`
                axios.post(checkUrl, {
                    email: memberEmail,
                    sendType: "enroll"
                }).then((res)=>{
                    setAuthNum(res.data);
                })
            }
        })
        .catch((error) => console.log(error));
    }

    const CheckAuth = () =>{
        if (inputNum.toString() !== authNum.toString()){
            window.alert(" .  .");
            setCheckEmail(false);
        }
        else{
            const checkUrl = `/api/opentalk/enroll/mailauthCheck`
            axios.post(checkUrl, {
                email: memberEmail,
                authNum: String(authNum)
            }).then((res)=>{
                if (res.data === "ok"){
                    alert(".");
                    setCheckEmail(true);
                }
                else{
                    if (checkEmail){
                        alert(" .");
                    }
                    else{
                        alert(" .  .");
                        setCheckEmail(false);
                    }
                }
            })
        }
    }

    const CheckAll = () =>{
        if (!checkId){
            alert("    .");
        }
        else if (!checkNickName){
            alert("    .");
        }
        else if (!checkEmail){
            alert("   .");
        }
        else{
            const url = `/api/opentalk/auth/signup`;
            axios.post(url,{
                memberId: memberId,
                memberPassword: memberPw,
                memberName: memberName,
                memberNickName: memberNickName,
                memberEmail: memberEmail,
                imgUrl: "https://storage.googleapis.com/opentalk-bucket/profile_prototype"
            }).then((res)=>{
    
            }).catch((error)=> {
                if (error.response){
                    setErrorMessage(error.response.data);
                } else{
                    setErrorMessage("");
                    console.log(error);
                }
            });
            alert(" .")
            navigate("/opentalk/member/login");
        }
        
    }

    return(
        <Container>
            <Row>
                <Col md={{ span: 6, offset: 3 }} className="border border-#7B7B7B border-3 rounded-1 p-5" style={{backgroundColor:"#7B7B7B"}}>
                    <h2 style={{color:"white"}}></h2>
                    <Form.Label style={{color:"white"}}></Form.Label>
                    <InputGroup>
                        <FormControl
                            type="text"
                            value={memberId}
                            onChange={GetInputId}
                            style={{borderTopLeftRadius: "25px",
                                    borderBottomLeftRadius: "25px"}}
                        ></FormControl>
                        <Button variant='#CDCDCD' style={{backgroundColor:"#CDCDCD",
                                                            borderTopRightRadius: "25px",
                                                            borderBottomRightRadius: "25px"}} onClick={CheckIdDuplicate}>  </Button>
                    </InputGroup>
                    <Form.Label style={{color:"white"}}></Form.Label>
                    <InputGroup>
                        <FormControl
                            type="password"
                            value={memberPw}
                            onChange={GetInputPw}
                            style={{borderTopLeftRadius: "25px",
                                    borderBottomLeftRadius: "25px",
                                    borderTopRightRadius: "25px",
                                    borderBottomRightRadius: "25px"}}
                        ></FormControl>
                    </InputGroup>
                    <Form.Label style={{color:"white"}}></Form.Label>
                    <InputGroup>
                        <FormControl
                            type="text"
                            value={memberName}
                            onChange={GetInputName}
                            style={{borderTopLeftRadius: "25px",
                                    borderBottomLeftRadius: "25px",
                                    borderTopRightRadius: "25px",
                                    borderBottomRightRadius: "25px"}}
                        ></FormControl>
                    </InputGroup>
                    <Form.Label style={{color:"white"}}></Form.Label>
                    <InputGroup>
                        <FormControl
                            type="text"
                            value={memberNickName}
                            onChange={GetInputNickName}
                            style={{borderTopLeftRadius: "25px",
                                    borderBottomLeftRadius: "25px"}} 
                        ></FormControl>
                        <Button variant='#CDCDCD' style={{backgroundColor:"#CDCDCD",
                                                        borderTopRightRadius: "25px",
                                                        borderBottomRightRadius: "25px"}} onClick={CheckNickNameDuplicate}>  </Button>
                    </InputGroup>
                    <Form.Label style={{color:"white"}}></Form.Label>
                    <InputGroup>
                        <FormControl
                            type="email"
                            value={memberEmail}
                            onChange={GetInputEmail}
                            style={{borderTopLeftRadius: "25px",
                                    borderBottomLeftRadius: "25px"}} 
                        ></FormControl>
                        <Button variant='#CDCDCD' style={{backgroundColor:"#CDCDCD",
                                                        borderTopRightRadius: "25px",
                                                        borderBottomRightRadius: "25px"}} onClick={CheckMail}> </Button>
                    </InputGroup>
                    <Form.Label style={{color:"white"}}></Form.Label>
                    <InputGroup>
                        <FormControl
                            type="text"
                            value={inputNum}
                            onChange={GetInputNum}
                            style={{borderTopLeftRadius: "25px",
                                    borderBottomLeftRadius: "25px"}} 
                        ></FormControl>
                        <Button variant='#CDCDCD' style={{backgroundColor:"#CDCDCD",
                                                        borderTopRightRadius: "25px",
                                                        borderBottomRightRadius: "25px"}} onClick={CheckAuth}></Button>
                    </InputGroup>
                    <br></br>
                    <Button variant='#CDCDCD' style={{backgroundColor:"#CDCDCD",
                                                        borderTopLeftRadius: "25px",
                                                        borderBottomLeftRadius: "25px",
                                                        borderTopRightRadius: "25px",
                                                        borderBottomRightRadius: "25px"}} onClick={CheckAll}></Button>
                </Col>
            </Row>
        </Container>
    );
}
export default EnrollComponent;